import { defineComponent, onMounted, ref } from "vue";
import { GUI } from "dat.gui";
export default defineComponent({
  props: {
    eventObj: {
      type: Object
    },
    option: {
      type: Object
    }
  },
  setup(props) {
    const controlPanel = ref();
    const initControlPanel = eventObj => {
      const gui = new GUI({
        autoPlace: false
      });
      controlPanel.value?.appendChild(gui.domElement);
      gui.add({
        : props.option?.parameter.speedFactor
      }, "", props.option?.constraints.minSpeedFactor, props.option?.constraints.maxSpeedFactor).onChange(eventObj.speedFactor);
      gui.add({
        : props.option?.parameter.tracksNumber
      }, "", props.option?.constraints.minTrajectoryNum, props.option?.constraints.maxTrajectoryNum).onChange(eventObj.tracksNumber);
      gui.add({
        : props.option?.parameter.segmentNumber
      }, "", props.option?.constraints.minSegmentNum, props.option?.constraints.maxSegmentNum).onChange(eventObj.segmentNumber);
      gui.add({
        : props.option?.parameter.fillWidth
      }, "", props.option?.constraints.minFillWidth, props.option?.constraints.maxFillWidth).onChange(eventObj.fillWidth);
      gui.add({
        : props.option?.parameter.aaWidth
      }, "", props.option?.constraints.minAAWidth, props.option?.constraints.maxAAWidth).onChange(eventObj.aaWidth);
      gui.add({
        : 0
      }, "", [0, 1, 2]).onChange(eventObj.color);
      gui.add({
        : 1
      }, "", [0, 1]).onChange(eventObj.primitive);
      gui.add({
        : props.option?.parameter.fixedDropRate
      }, "", props.option?.constraints.minFixedDropRate, props.option?.constraints.maxFixedDropRate).onChange(eventObj.fixedDropRate);
      gui.add({
        : props.option?.parameter.extraDropRate
      }, "", props.option?.constraints.minExtraDropRate, props.option?.constraints.maxExtraDropRate).onChange(eventObj.extraDropRate);
    };
    onMounted(() => {
      initControlPanel(props.eventObj);
    });
    return {
      controlPanel
    };
  }
});